<template>
	<view class="register">
		<view class="formBox" style="margin-top: 20rpx;">
			<u-form :model="form" ref="uForm" :error-type="errorType">
				<u-form-item label="交通安排" prop="sushe" right-icon="arrow-right" label-width='180'>
					<u-input v-model="form.txt1" :disabled="true" input-align="right"
						placeholder="请选择" @click="Toselect('transportation_arrangements')" />
				</u-form-item>
				<u-form-item label="用餐安排" prop="sushe" right-icon="arrow-right" label-width='180'>
					<u-input v-model="form.txt2" @click="Toselect('meal_arrangements')" :disabled="true" input-align="right"
						placeholder="请选择" />
				</u-form-item>
				<u-form-item label="住宿安排" prop="sushe" right-icon="arrow-right" label-width='180'>
					<u-input v-model="form.txt3" @click="Toselect('accommodation_arrangements')" :disabled="true" input-align="right"
						placeholder="请选择" />
				</u-form-item>
				<u-form-item label="是否允许超员报名" prop="isOverstaffRegistration"  required label-width='260'>
					<view class="yuanbox">
					<u-radio-group v-model="form.isOverstaffRegistration">
						<u-radio name="1">是</u-radio>
						<u-radio name="2">否</u-radio>
					</u-radio-group>
					</view>
				</u-form-item>
				<u-form-item label="允许超员名额" prop="registrationNum" label-width='180' v-show="form.isOverstaffRegistration==1">
					<u-input type="tel" v-model="form.registrationNum" input-align="right" placeholder="请输入名额" />
				</u-form-item>
			</u-form>
			<u-button @click="submit" type="primary" style="margin-top: 50rpx;">提交</u-button>
		</view>
		<!-- 选择器 -->
		<u-select v-model="sushe_show" mode="single-column" :list="susheArr" @confirm="sushe_confirm"
		value-name='dictValue' label-name='dictLabel'></u-select>
	  <u-toast ref="uToast"></u-toast>
	</view>
</template>

<script>
	import {
		getId,
		getDicts,
		updateAct
	} from '../../api/index.js'
	import request from '@/api/request.js'
	export default {
		data() {
			return {
				errorType: ['toast'],
				selectTips:null,
				susheArr: [],
				sushe_show: false,
				action: request.ROOTHTTP+"/file/upload",
				fileList: [],
				form: {
					name: '',
					intro: '',
				},
				rules: {
					isOverstaffRegistration:[{
						required: true,
						message: '请选择是否允许超员报名',
						trigger: ['change', 'blur'],
					}]
				}
			}
		},
		onLoad() {
					this.form.id=this.$route.query.id
				
		},
		methods: {
			Toselect(no) {
				this.selectTips = no;
				getDicts(this.selectTips).then(res => { //系统是否
					if (res.code == 200) {
						this.susheArr = res.data;
						this.sushe_show = true;
					}
				})
			},
			sushe_confirm(e) {
				if (this.selectTips == 'transportation_arrangements') {
					this.form.txt1 = e[0].label;
					this.form.trafficArrangement = e[0].value;
				} else if (this.selectTips == 'meal_arrangements') {
					this.form.txt2 = e[0].label;
					this.form.mealArrangement = e[0].value;
				} else if (this.selectTips == 'accommodation_arrangements') {
					this.form.txt3 = e[0].label;
					this.form.accommodationArrangements = e[0].value;
				}
			},
			submit() {
					if (this.form.isOverstaffRegistration) {
						updateAct(this.form).then(res => {
							if (res.code == 200) {
								this.$refs.uToast.show({
									title: '提交成功'
								});
							}
						});
					} else {
						this.$refs.uToast.show({
							title: '请选择是否允许超员报名'
						});
					}
			}
		}
	}
</script>

<style scoped>
	.register {
		background-color: #F5F6FA;
		width: 100vw;
		height: 100vh;
	}
    .register /deep/ .u-list-item{
		width: 125rpx!important;
		height: 125rpx!important;
	}
	.register .title {
		width: 100%;
		text-align: center;
		font-size: 30rpx;
		font-weight: bold;
		color: #333333;
		padding: 32rpx 0 25rpx 0;
	}
	.yuanbox {
		position: absolute;
		right: 0;
	}
	
	.yuanbox /deep/ .u-list-item {
		width: 80rpx !important;
		height: 80rpx !important;
		border-radius: 50%;
	}

	.mybox {
		width: 100%;
		text-align: center;
	}

	.mybox /deep/ .u-upload {
		justify-content: center;
	}

	.formBox {
		padding: 0 30rpx 30rpx 30rpx;
		background-color: #fff;
	}

	.mititle {
		font-size: 30rpx;
		font-weight: bold;
		color: #333333;
		height: 90rpx;
		line-height: 90rpx;
		margin: 0 -30rpx;
		padding: 0 30rpx;
		border-bottom: 1px solid #f1f1f1;
	}

	.smtitle {
		width: 100%;
		text-align: center;
		font-size: 24rpx;
		color: #B1B1B1;
	}

	.smtitle b {
		color: #FF9D1D;
	}
</style>